}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.wrapper {
  width: 320px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 660px) {
  .wrapper {
    width: 660px;
  }
}
@media (min-width: 1200px) {
  .wrapper {
    width: 100%;
  }
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .main-nav {
    position: relative;
