    background-color: rgba(0, 0, 0, 0.3);
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 1200px) {
  .main-nav__list {
    width: 940px;
    margin-left: auto;
    margin-right: auto;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
}
.main-nav__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 63px;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  color: #ffffff;
  background-color: #283645;
  border-bottom: 1px #000000 solid;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
  z-index: 1;
}
.main-nav__item:last-child {
  border-bottom: none;
}
@media (min-width: 1200px) {
  .main-nav__item {
    background-color: transparent;
    min-height: 110px;
    width: 150px;
    border-bottom: none;
    box-shadow: none;
  }
  .main-nav__item:nth-child(n+1) {
    margin-right: 130px;
  }
  .main-nav__item:nth-child(n+2) {
    margin-right: 20px;
  }
  .main-nav__item:nth-child(n+3) {
    margin-right: 20px;
  }
  .main-nav__item:nth-child(n+4) {
    margin-right: 20px;
  }
  .main-nav__item:nth-child(n+5) {
    text-align: right;
    margin-right: 0;
  }
}
.main-nav__item--top {
  min-height: 69px;
}
@media (min-width: 660px) {
